.collection {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
}

.collection__item {
  margin: 0 0 5px 0;

  max-height: 90vh;
}

@media only screen and (max-height: 420px) {
  .collection_nowrap_landscape {
    -ms-flex-flow: row;
        flex-flow: row;
  }

  .collection_nowrap_landscape .collection__item {
    margin: 0 5px 0 0;

    max-width: 90vw;
    height: 100vh;
    max-height: unset;
  }
}

@media only screen and (min-width: 768px) and (min-height: 421px) {
  .collection {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(334px, 1fr));
    grid-auto-rows: 45vh;
    grid-gap: 5px;
  }

  .collection__item {
    width: 100%;
    height: 100%;
  }
}



